___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  text-align: center;
}

@font-face {
  font-family: bungee;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

body > h2 {
  font-family: bungee;
  font-size: 3rem;
  margin-top: 50px;
}

body {
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
    url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    sans-serif;
}

#game {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  gap: 50px;

  .gameboard {
    grid-row: 2;
    display: grid;
    border: 5px solid rgba(0, 0, 0, 0.6);
    background-color: rgb(184, 184, 184, 0.3);

    gap: 4px;
    > div {
      display: flex;
      gap: 4px;

      > div {
        background-color: rgba(0, 0, 0, 0.15);
        width: 40px;
        height: 40px;
      }

      > div:hover {
        background-color: rgba(0, 0, 0, 0.4);
      }
    }
  }

  p {
    font-size: 1.5rem;
    font-weight: 500;
    color: white;
  }

  .player {
    pointer-events: none;
  }
}

dialog {
  text-align: center;
  font-size: 1rem;
  width: 300px;
  border: none;
  box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.2);
  border-radius: 5px;

  button {
    font-size: 1rem;
    font-weight: bold;
    background-color: #77858d;
    color: #f5faf4;
    border: 0px transparent;
    border-radius: 6px;
    padding: 8px 20px;
    cursor: pointer;
  }
}

dialog::backdrop {
  background-color: #a8bdc8;
  opacity: 0.5;
}
